// /app/page.tsx
// 根路径页面：自动重定向到默认语言（英文）的 connect 页面
import { redirect } from 'next/navigation';

/**
 * 根路径组件（对应访问 https://你的域名/ 时触发）
 * 作用：无需用户手动输入语言前缀，自动跳转到 /en/connect
 */
export default function RootHomePage() {
  // 重定向逻辑：如果需要默认中文，可改为 redirect('/zh/connect')
  redirect('/en/connect');

  // 注：redirect 会直接跳转，下方 return 实际不会执行，仅为语法完整性
  return null;
}